.hero {
    background-image: linear-gradient(to bottom, transparent, transparentize($mdBlue,0.5) 90%), 
                      radial-gradient(ellipse 100% 150% at center bottom, $mdBlue, $dkBlue 80%);
    background-blend-mode: lighten, normal;   
    background-size:cover;
    background-position:center bottom;
    height:90%;
    padding:3em;
    perspective:500px;
    z-index:1;
    overflow:hidden;

    @media only screen and (max-height:800px){
        height:110%;
    }
}

.hero__spacer {
    display:none;
    height:90%;
    position:relative;
    z-index:0;
}
.hero__content {
    position:relative;
    top:25%;
    transform-origin:50% 100%;
    z-index:3;
    transition:all 200ms ease;

    @media only screen and (max-height:750px) {
        top:15%;
    }

}

.hero__logo {
    width:100%;
    text-align:center;

    img {
        width:50%;
        max-width:220px;
        min-width:120px;
    }
}
$borderWidth : 5px; 
.hero__text {
    width:100%;
    max-width:600px;
    position:relative;
    margin:0 auto;
    text-align:center;
    color:#FFF;
    z-index:5;

    h1 {
        //overwrite the font-family in variables/fonts
        font-family:'soleil', helvetica, sans-serif;
        font-size:40px;
        margin-top:.25em;
        font-weight:700;
        text-transform:uppercase;

        small {
            display:flex;
            align-items:center;
            justify-content:center;
            margin-top:.5em;
            font-size:28px;
            font-weight:500;

            span {
                flex: 1 1 0;
                opacity:0.6;
                letter-spacing:1px;
            }

            span.divider {
                flex: 0 0 1em;
                font-size:1.25em;
                font-weight:500;
                opacity:1;
            }
            span:first-child {text-align:right;}
            span:last-child {text-align:left;}
        }
    }

     @media only screen and (max-width:700px){
        h1 {font-size:28px;}
        h1 small {
            font-size:18px;
        }
    }

    &:before {
        //dashed circle
        //content:"";
        width:60%;
        height:0;
        padding-bottom:calc(60% - #{$borderWidth*2});
        position:absolute;
        left:50%;
        top:50%;
        border-radius:50%;
        border:$borderWidth dashed transparentize(#FFF, .6);
        transform-origin:50% 50%;
        transform:translate(-50%, -50%);
        //animation: spin 30s infinite linear;
    }
    &:after {
        position:absolute;
        width:100%;
        height:0;
        padding-bottom:35%;
        bottom:-50%;
    }
}

#stars {
    position:absolute;
    width:2000px; 
    height:2000px;
    top:50%;
    left:50%;
    transform:translate(-50%,-50%);
    animation:rotate 200s infinite forwards;
}

#fg-cloud,
#bg-cloud-1,
#bg-cloud-2 {
  position:absolute; 
  left:0;
  bottom:0;
  width:100%;
  overflow:hidden;
}

#fg-cloud {
  height:100%
}

#bg-cloud-1 {
    height:100%;
}

#bg-cloud-2 {
    height:100%;
}

svg {width:100%; height:100%;}

