html {
    height:100%;
}

body {
    height:100%;
    position:relative;
}

* {
    box-sizing:border-box;
}

#app {
    height:100%;
}

.container {position:relative;}

.animate-in {   
    * {
        transition:transform 1s ease, opacity 1.5s ease;
        transform:translateY(0);
        opacity:1;

        &.invisible {
           transform:translateY(-1em);
           opacity:0;
        }
    }
}

.scroll {
    transition:transform 50ms ease;
}


#about {
    display:block;
    height:600px;
    background-color:#FFF;
    &:before {
        content:"";
        height:20px;
        background-color:#FFF;
        bottom:100%;
        position:absolute;
        width:100%;
    }
}

main {
    position:relative;
    padding-top:50px;
    background-color:#FFF;
    z-index:3;
    overflow:hidden;

    * {
        position:relative;
    }
    

    h2 {
        text-align:right;
        margin:.5em auto;
        position:relative;
        text-align: center;
        transform-origin:50% 50%;
        //transform:translateX(10%) rotate(5deg);
        
        span {
            position:relative;            
            display:inline-block;
            background-color:#FFF;
            position:relative;
            padding:0 1em;
        }

        &:before {
            content:"";
            height:50%;
            width:120%;
            left:60%;
            top:50%;
            transform:translate(-50%, -55%);
            
            position:absolute;
            background-color:transparentize($ltBlue, 0.5);
        }
    } 
}


footer,
.footer {
    background-color:$mdBlue;
    height:4em;
    position:relative;
    //z-index:3;
}